<template>
  <div class="ebook">
    <TitleBar :menuShow='menuShow'></TitleBar>
    <div class="read-wrapper">
        <div id="read"></div>
        <div class="mask">
            <div class="left" @click="prevPage"></div>
            <div class="center" @click="toggleMenu"></div>
            <div class="right" @click="nextPage"></div>
        </div>
    </div>
    <MenuBar :menuShow='menuShow' 
        :fontSizeList='fontSizeList' 
        :defaultFontSize='defaultFontSize' 
        @setFontSize='setFontSize'
        :themesList='themesList'
        :defaultTheme='defaultTheme'
        @setTheme='setTheme'
        :bookAvailable='bookAvailable'
        @onProgressChange='onProgressChange'
        @jumpTo='jumpTo'
        :navigation='navigation'
        ref="menuBar"></MenuBar>
  </div>
</template>

<script>
import MenuBar from '@/components/MenuBar'
import TitleBar from '@/components/TitleBar'
import Epub from 'epubjs'
const Download_Url = './static/三体.epub'

export default {
    components: {
        MenuBar: MenuBar,
        TitleBar: TitleBar,
    },
    data: function() {
        return {
            menuShow: false,
            fontSizeList: [
                {fontSize: 12,},
                {fontSize: 14,},
                {fontSize: 16,},
                {fontSize: 18,},
                {fontSize: 20,},
                {fontSize: 22,},
                {fontSize: 24,},
            ],
            defaultFontSize: 14,
            themesList: [
                {
                    name: 'default',
                    style: {
                        body: {'color': '#000','background': '#fff'}
                    },
                },
                {
                    name: 'eye',
                    style: {
                        body: {'color': '#000','background': '#ceeaba'}
                    },
                },
                {
                    name: 'night',
                    style: {
                        body: {'color': '#fff','background': '#000'}
                    },
                },
            ],
            defaultTheme: 0,
            // 图书是否处于可用状态
            bookAvailable: false,
            // 目录
            navigation: [],
        }
    },
    methods: {
        // 根据链接跳转到指定位置
        jumpTo: function(href) {
            this.rendition.display(href)
            this.hideBar()
        },
        // 隐藏所有弹出 bar
        hideBar: function() {
            // 隐藏菜单栏和设置栏
            this.menuShow = false
            // 隐藏设置栏弹出的 bar
            this.$refs.menuBar.hideSetting()
            this.$refs.menuBar.hideContent()            
        },
        // progress 进度条数值 （0-100）
        onProgressChange: function(progress) {
            let percentage = progress / 100
            // 获取位置
            let location = 0
            if (percentage > 0) {
                location = this.locations.cfiFromPercentage(percentage)
            }
            // 显示
            this.rendition.display(location)
        },
        setTheme: function(index) {
            this.themes.select(this.themesList[index].name)
            this.defaultTheme = index
        },
        registerThemes: function() {
            this.themesList.forEach(theme => {
                this.themes.register(theme.name, theme.style)
            })
        },
        setFontSize: function(fontSize) {
            this.defaultFontSize = fontSize
            if (this.themes) {
                this.themes.fontSize(fontSize + 'px')
            }
        },
        toggleMenu: function() {
            this.menuShow = !this.menuShow
            if (!this.menuShow) {
                this.$refs.menuBar.hideSetting()
            }
        },
        // 电子书的解析和渲染
        showEpub: function() {
            // 生成 Ebook
            this.book = new Epub(Download_Url)
            // 生成 Rendition, 通过 book.renderTo(id, {})
            this.rendition = this.book.renderTo('read', {
                width: window.innerWidth,
                height: window.innerHeight,
            })
            // 通过 Rendition.display 渲染电子书
            this.rendition.display()
            // 获取 themes
            this.themes = this.rendition.themes
            // 设置默认字体
            this.setFontSize(this.defaultFontSize)
            // this.themes.register(name, styles)
            this.registerThemes()
            // this.themes.select(name)
            this.setTheme(this.defaultTheme)
            // 获取 location 对象 (默认为空)
            // console.log(this.book.locations)
            // 通过 epubjs 的钩子函数实现
            this.book.ready.then(() => {
                this.navigation = this.book.navigation
                return this.book.locations.generate()
            }).then(result => {
                this.locations = this.book.locations
                // 此时图书可用了
                this.bookAvailable = true
            })
        },
        // 翻页
        prevPage: function() {
            // rendition.prev
            if (this.rendition) {
                this.rendition.prev()
            }
        },
        nextPage: function() {
            // rendition.next    
            if (this.rendition) {
                this.rendition.next()
            }        
        },
    },
    mounted: function() {
        this.showEpub()
    }

}

</script>
<style lang='scss' scoped>
@import 'assets/styles/global';
.ebook {
    position: relative;
    .read-wrapper {
        .mask {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 100;
            display: flex;
            // background: blue;
            .left {
                flex: 0 0 px2rem(100);
            }
            .center {
                flex: 1;
            }
            .right {
                flex: 0 0 px2rem(100);
            }
        }
    }
}
</style>


