<template>
  <div id="app">
    <!-- <span class="icon-back"></span> -->
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}

document.addEventListener('DOMContentLoaded', () => {
  const html = document.querySelector('html')
  let fontSize = window.innerWidth / 10
  if (fontSize > 50) {
    fontSize = 50
  }
  html.style.fontSize = `${fontSize}px`
})
</script>

<style>
