<template>
  <div>
      <transition name="slide-down">
        <div class="title-wrapper" v-show="menuShow">
            <div class="left">
                <span class="icon-back icon"></span>
            </div>
            <div class="right">
                <div class="icon-wrapper">
                    <span class="icon-cart icon"></span>
                </div>
                <div class="icon-wrapper">
                    <span class="icon-person icon"></span>
                </div>
                <div class="icon-wrapper">
                    <span class="icon-more icon"></span>
                </div>
            </div>
        </div>
      </transition>
  </div>
</template>

<script>
export default {
    props: {
        menuShow: {
            type: Boolean,
            default: false,
        },
    },
}

</script>
<style lang='scss' scoped>
@import '../assets/styles/global';
.title-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 101;
        width: 100%;
        height: px2rem(48);
        display: flex;
        box-shadow: 0 px2rem(8) px2rem(8) rgba(0, 0, 0, 0.15);
        background: white;
        .left {
            flex: 0 0 px2rem(60);
            @include center;
        }
        .right {
            flex: 1;
            display: flex;
            justify-content: flex-end;
            .icon-wrapper {
                flex: 0 0 px2rem(40);
                @include center;
            }
        }
    }
</style>


