<template>
  <div class="menu-bar">
      <transition name="slide-up">
        <div class="menu-wrapper" :class="{'hide-box-shadow': settingShow || !menuShow}" v-show="menuShow">
            <div class="icon-wrapper">
                <span class="icon-menu icon" @click="showSetting('menu')"></span>
            </div>
            <div class="icon-wrapper">
                <span class="icon-progress icon" @click="showSetting('progress')"></span>
            </div>
            <div class="icon-wrapper">
                <span class="icon-bright icon" @click="showSetting('theme')"></span>
            </div>
            <div class="icon-wrapper">
                <span class="icon-a icon" @click="showSetting('fontsize')">A</span>
            </div>
        </div>
      </transition>
      <transition name="slide-up">
        <div class="setting-bar" v-show="settingShow">
            <div class="setting-font-size" v-if="showTag === 'fontsize'">
                <div class="preview" :style="{fontSize: fontSizeList[0].fontSize + 'px'}">A</div>
                <div class="select">
                    <div class="select-wrapper" v-for="(item, index) in fontSizeList" 
                        :key="index"
                        @click="setFontSize(item.fontSize)">
                        <div class="line"></div>
                        <div class="point-wrapper">
                            <div class="point" v-show="defaultFontSize === item.fontSize">
                                <div class="small-point"></div>
                            </div>
                        </div>
                        <div class="line"></div>
                    </div>
                </div>
                <div class="preview" :style="{fontSize: fontSizeList[fontSizeList.length - 1].fontSize + 'px'}">A</div>
            </div>
            <div class="setting-theme" v-else-if="showTag === 'theme'">
                <div class="setting-theme-item" v-for="(item, index) in themesList" 
                    :key='index'
                    @click="setTheme(index)">
                    <div class="preview" :style="{background: item.style.body.background}"></div>
                    <div class="text" :class="{selected: index === defaultTheme}">{{item.name}}</div>
                </div>
            </div>
            <div class="setting-progress" v-else-if="showTag === 'progress'">
                <div class="progress-wrapper">
                    <input class="progress" type="range"
                                            max="100"
                                            min="0"
                                            step="1"
                                            @change="onProgressChange($event.target.value)"
                                            @input="onProgressInput($event.target.value)"
                                            :value="progress"
                                            :disabled="!bookAvailable"
                                            ref="progress">
                </div>
                <div class="text-wrapper">
                    <span>{{bookAvailable ? progress + '%' : '加载中...'}}</span>
                </div>
            </div>
        </div>
      </transition>
      <content-view :bookAvailable='bookAvailable'
                    :navigation='navigation'
                    v-show="contentShow"
                    @jumpTo='jumpTo'></content-view>
      <transition name="fade">
          <div class="mask"
                v-show="contentShow"
                @click="hideContent"></div>
      </transition>
  </div>
</template>

<script>
import ContentView from '@/components/ContentView'

export default {
    components: {
        ContentView,
    },
    props: {
        menuShow: {
            type: Boolean,
            default: false,
        },
        fontSizeList: Array,
        defaultFontSize: Number,
        themesList: Array,
        defaultTheme: Number,
        bookAvailable: Boolean,
        navigation: Object,
    },
    data: function() {
        return {
            settingShow: false,
            contentShow: false,
            showTag: '',
            progress: 0,
        }
    },
    methods: {
        // 跳转章节
        jumpTo: function(href) {
            this.$emit('jumpTo', href)
        },
        // 进度条松开，跳转到相应位置
        onProgressChange: function(progress) {
            this.$emit('onProgressChange', progress)
        },
        // 拖动进度条触发事件
        onProgressInput: function(progress) {
            this.progress = progress
            this.$refs.progress.style.backgroundSize = `${progress}% 100% `
            // console.log(this.$refs.progress.backgroundSize)
        },
        setTheme: function(index) {
            this.$emit('setTheme', index)
        },
        setFontSize: function(fontSize) {
            this.$emit('setFontSize', fontSize)
        },
        showSetting: function(tag) {
            if (tag !== 'menu') {
                this.settingShow = true
                this.showTag = tag
            } else {
                this.settingShow = false
                this.contentShow = true
            }
        },
        hideSetting: function() {
            this.settingShow = false            
        },
        hideContent: function() {
            this.contentShow = false
        },
    },
}

</script>
<style lang='scss' scoped>
@import '../assets/styles/global';
.menu-bar {
    .menu-wrapper {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: px2rem(48);
        display: flex;
        box-shadow: 0 px2rem(-8) px2rem(8) rgba(0, 0, 0, 0.15);
        background: white;
        z-index: 102;
        .icon-wrapper {
            flex: 1;
            @include center;
            .icon-progress {
                font-size: px2rem(24);
            }
        }
        &.hide-box-shadow {
            box-shadow: none;
        }
    }
    .setting-bar {
        position: absolute;
        bottom: px2rem(48);
        left: 0;
        height: px2rem(60);
        width: 100%;
        background: white;
        box-shadow: 0 px2rem(-8) px2rem(8) rgba(0, 0, 0, 0.15);
        z-index: 101;
        .setting-font-size {
            display: flex;
            height: 100%;
            .preview {
                flex: 0 0 px2rem(40);
                @include center;
            }
            .select {
                flex: 1;
                display: flex;
                .select-wrapper {
                    &:first-child {
                        .line {
                            &:first-child {
                                border-top: none;
                            }
                        }
                    }
                    &:last-child {
                        .line {
                            &:last-child {
                                border-top: none;
                            }
                        }
                    }
                    flex: 1;
                    display: flex;
                    align-items: center;
                    .line {
                        flex: 1;
                        height: 0;
                        border-top: px2rem(1) solid #ccc;
                    }
                    .point-wrapper {
                        position: relative;
                        flex: 0 0 0;
                        width: 0;
                        height: px2rem(7);
                        border-left: px2rem(1) solid #ccc;
                        .point {
                            position: absolute;
                            top: px2rem(-6);
                            left: px2rem(-8);
                            width: px2rem(18);
                            height: px2rem(18);
                            border-radius: 50%;
                            border: px2rem(1) solid #ccc;
                            box-shadow: 0 px2rem(4) px2rem(4) rgba(0, 0, 0, 0.15);
                            background: white;
                            @include center;
                            .small-point {
                                height: px2rem(5);
                                width: px2rem(5);
                                background: #000;
                                border-radius: 50%;
                            }
                        }
                    }
                }
            }
        }
        .setting-theme {
        display: flex;
        height: 100%;
            .setting-theme-item {
                flex: 1;
                display: flex;
                flex-direction: column;
                box-sizing: border-box;
                padding: px2rem(5);
                .preview {
                    flex: 1;
                    border: px2rem(1) solid #ccc;
                }
                .text {
                    flex: 0 0 px2rem(20);
                    font-size: px2rem(14);
                    color: #ccc; 
                    @include center;
                    &.selected {
                        color: #333;
                    }
                }
            }
        }
        .setting-progress {
            position: relative;
            width: 100%;
            height: 100%;
            .progress-wrapper {
                width: 100%;
                height: 100%;
                box-sizing: border-box;
                padding: 0 px2rem(30);
                @include center;
                .progress {
                    width: 100%;
                    appearance: none;
                    height: px2rem(2);
                    background: linear-gradient(#999, #999) no-repeat, #ddd;
                    background-size: 0 100%;
                    &:focus {
                        outline: none;
                    }
                    &::-webkit-slider-thumb {
                        appearance: none;
                        height: px2rem(20);
                        width: px2rem(20);
                        border-radius: 50%;
                        background: white;
                        box-shadow: 0 px2rem(4) px2rem(4) rgba(0, 0, 0, 0.15);
                        border: px2rem(1) solid #ddd;
                    }
                }
            }
            .text-wrapper {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                color: #333;
                font-size: px2rem(12);
                text-align: center;
            }
        }
        
    }
    .mask {
        position: absolute;
        display: flex;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 101;
        background: rgba(55, 55, 55, 0.51);
    }
}
    

</style>


